import React, { Component } from 'react';

class About extends Component {
  componentDidMount() {
    window.Spinner.quitar();
  }
  verCredencial(e, referencia, tipo, detalles) {
    e.preventDefault();
    var modal = document.getElementById("myModal");
    var modalImg = document.createElement('img');
    document.getElementById("divModalImagen").appendChild(modalImg);
    modalImg.setAttribute("id", "img01");
    modalImg.setAttribute("class", "modal-content");
    var captionText = document.getElementById("caption");
    modal.style.display = "block";
    modalImg.src = './img/credenciales/'+referencia+'.'+tipo;
    captionText.innerHTML = referencia.toUpperCase()+detalles.toUpperCase();
    var span = document.getElementsByClassName("closeModal")[0];
    span.onclick = function() {
        document.getElementById("divModalImagen").innerHTML = "";
        modal.style.display = "none";
    }
  }
  render() {
    return (
      <div className='container-fluid'>
        <div className='row justify-content-center'>
          <div className="col-11 col-md-8">
            {/* <p>This is the <b>About</b> Component</p> */}
            <img id="fotoAbout" src="./img/antonio.jpg" className="img-fluid align-self-center rounded" alt=""/>
            <hr/>
            <p className="textoAbout">Hi, I'm Antonio. As an Electrical Engineer with a Photovoltaic Associate credential, I am prepared for the challenges of designing, promoting and implementing renewable energy solutions within the energy-related industry. I have a fundamental understanding of energy engineering and a sense of social responsibility for the implementation of sustainable energy solutions. Experience in evaluation, analysis, design and installation of site data in the solar industry, providing AUTOCAD design including single and three line diagrams, electrical system designs and design interpretations.
            </p>
            <hr/>
          </div>
        </div>
        
        <div className='row justify-content-center textoAboutCredenciales mb-3'>

        <div className="col-4 col-md-4">
            <h6><b>Degrees</b></h6>
            <div className="divCredenciales">
              <div>
                <span>ENG</span><br/><span onClick={(e) => this.verCredencial(e,'ingenieria','jpg','')} href='#'><i className="fa fa-user-graduate fa-3x" title="Click to see the credential"></i></span>
              </div>
              
            </div>
          </div>
        </div>

        <div className='row justify-content-center textoAboutCredenciales mb-3'>
          <div className="col-11 col-md-9">
            <h6><b>Certifications</b></h6>
            <div className="divCredenciales">
              <div>
                <span>IGNITION</span><br/><span onClick={(e) => this.verCredencial(e,'ignition','png','')} href='#'><i className="fa fa-certificate fa-3x" title="Inductive Automation Credential | Click to see the credential"></i></span>
              </div>
              <div>
                <span>AUTODESK</span><br/><a href='https://www.youracclaim.com/badges/50bee2b3-40e8-4f10-b065-692828549482' target='_blank' rel="noopener noreferrer"><i className="fa fa-certificate fa-3x" title="Autodesk AutoCAD Certified User | Click to see the credential"></i></a>
              </div>
              <div>
                <span>NABCEP</span><br/><a href={"https://www.nabcep.org/nabcep-associates/?last_name=Miranda&country=USA&state=&certification_type=PVA"} target='_blank' rel="noopener noreferrer"><i className="fa fa-certificate fa-3x" title="NABCEP Photovoltaic Associate Credential | Click to see the credential"></i></a>
              </div>
            </div>
          </div>

          <div className="col-8 col-md-6">
            <div className="divCredenciales">
              
              <div>
                <span>UNIFAMILY</span><br/><span onClick={(e) => this.verCredencial(e,'unifamily','png','')} href='#'><i className="fa fa-certificate fa-3x" title="Proyectos de Instalaciones Eléctricas Nivel 1: Viviendas Unifamiliares | Click to see the credential"></i></span>
              </div>
              <div>
                <span>MULTIFAMILY</span><br/><span onClick={(e) => this.verCredencial(e,'multifamily','png','')} href='#'><i className="fa fa-certificate fa-3x" title="Proyectos de Instalaciones Eléctricas Nivel 2: Viviendas Multifamiliares | Click to see the credential"></i></span>
              </div>
              
            </div>
          </div>
          
          
        </div>
        <div className='row justify-content-center mb-2'>
          <div className="col-11 col-md-8">
          <hr/>
            <a className="btn btn-light" href='./docs/Profile.pdf' target="_blank" rel="noopener noreferrer"><b>Resume</b></a>
          </div>
        </div>
      </div>
    );
  }
}

export default About;